.couple-section {
	position: relative;
	background-color: var(--color-primary);
	color: var(--color-dark);
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 36px;
			font-family: $font-secondary;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 16/8;
		@include max-md {
			aspect-ratio: 6/5;
		}
		.image-element {
			height: 100%;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 8rem 0;
		@include max-md {
			padding: 5rem 0;
		}
		.corner-1 {
			position: absolute;
			top: 0;
			left: 0;
			width: 40%;
			@include max-lg {
				width: 55%;
			}
		}
		.corner-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 40%;
			transform: scaleX(-1);
			@include max-lg {
				width: 55%;
			}
		}
		.corner-3 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 40%;
			transform: scaleY(-1);
			@include max-lg {
				width: 55%;
			}
		}
		.corner-4 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 40%;
			transform: scale(-1);
			@include max-lg {
				width: 55%;
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		text-align: left;
		margin-bottom: 5rem;
		@include max-sm {
			margin-bottom: 3rem;
		}
		.title-section {
			text-align: left;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 3/4;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					width: 100%;
					border-radius: var(--border-radius);
				}
			}
			.ornaments-wrapper {
				border-radius: var(--border-radius);
				.corner-1,
				.corner-2,
				.corner-3,
				.corner-4 {
					width: 56%;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 1rem 1rem 0;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
